.Weather {
  display: flex;
  flex-direction: column;
  font-family: 'Anton', sans-serif;
}

.Weather_focused {
  font-size: 31px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 208vh;
  justify-content: space-around;
}

.Weather_focused img {
  width: 100px;
}

.Weather_forecast {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-around;
  flex-wrap: wrap;
}

.Weather_box {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  justify-content: center;
  cursor: pointer;
}

.Weather_box img {
  width: 50px;
}

.Weather_desc {
  text-transform: capitalize;
}
